import VisualComponent from "jx/core/comps/VisualComponent";
import Common from "jx/editor/editionitems/Common";
class Button extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.commonLib = new Common({ jx: this.jx });
        var itemData = { shortcut: true, id: `${this.dataObject.id}`, type: 'Group',
            children: [
                { id: `${this.dataObject.id}_CLICK`, type: 'Sound', description: 'description' },
                { id: `${this.dataObject.id}_NORMAL_I`, type: 'Image', source: this.dataObject.source, asset: this.dataObject.asset, description: 'description' },
                { id: `${this.dataObject.id}_CLICK_I`, type: 'Image', source: this.dataObject.source, asset: this.dataObject.asset, description: 'description' }
            ]
        };
        this.item = itemData;
    }
}
export default Button;
